export default {
  data () {
    return {
      menuList: [
        '全部',
        '待付款',
        '待收货',
        '退款/退货'
      ],
      activeIndex: 0,
      orderList: []
    }
  },
  onLoad (options) {
    this.activeIndex = parseInt(options.index)
    this.queryOrderList()
  },
  methods: {
    clickSearch (index) {
      this.activeIndex = index
      this.queryOrderList()
    },
    async queryOrderList () {
      let data = await this.$request({
        url: '/api/public/v1/my/orders/all?type=' + (this.activeIndex + 1),
        isAuth: true
      })

      this.orderList = data.orders
    }
  }
}
